.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1.5fr;
  grid-template-rows: 1fr;
  grid-gap: .5em;
}
.options input,
#upload {
  background: rgba(0, 0, 0, 0.15);
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 5px;
}
.options input:hover,
#upload:hover {
  background: rgba(0, 0, 0, 0.2);
}
.options input:focus,
#upload:focus {
  background: rgba(0, 0, 0, 0.3);
}
#upload {
  width: 100%;
}
